<template>
    <div>
        <router-link to="/">
            <div class="toMain">
            <div class="toMain__background">

            </div>
            <span class="toMain__arrow">&larr;</span>  
        </div>
        </router-link>        
        

        <h2> изменить {{ card.title }} </h2>
        
        <TodoList class="todos"
            v-bind:todos="card.todos"
            v-bind:place="'editor'"
            v-bind:id="id"
        />
    </div>
</template>

<script>
import { mapGetters } from 'vuex';
import TodoList from '@/components/TodoList'

export default {
    data() {
        return {
            card: [''],
        }
    },

    props: ['id'],

    methods: {
        removeTodo(id) {
            this.$emit('removeTodo', id)
        },
        
        find() {
            let el = {};
            this.getAllCards.forEach(element => {

                if(element.id === this.id[0]) {
                    el = element
                }
            });
            
            const index = this.getAllCards.indexOf(el);
            return index
        }

    },

    computed:{
        ...mapGetters(['getCardToEdit', 'getAllCards']),

    },

    async mounted() {
        this.card = await this.getCardToEdit(this.find())
    },

    components: {
        TodoList
    }
}

</script>

<style scoped>
    .toMain {
        margin-top: -60px;
        font-size: 3rem;
        width: 100px;
        height: 100vh;
        position: absolute;
    }

    .toMain__background {
        z-index: 1;
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0;
        background: linear-gradient(90deg, rgba(0,0,0,0.5) 0%, rgba(255,255,255,1) 100%);
        -webkit-transition: all 0.5s ease;;
        -moz-transition: all 0.5s ease;;
        -o-transition: all 0.5s ease;;
        transition: all 0.5s ease;
    }

    .toMain__arrow {
        position: absolute;
        opacity: 1;
    }

    .toMain__background:hover {
        opacity: 1;
    }

    .todos {
        width: 500px;
        margin: auto;
    }

</style>