<template>
    <div>
        <div class="card-list">
            <Card
                v-for="card of cards"
                :card="card"
                :key="card.id"
                v-bind="card"
            />
        </div>
    </div>
    
</template>

<script>
import Card from '@/components/Card'
import { mapGetters, mapActions } from "vuex"

export default {
    name: 'app',
    data() {
        return {
            cards: [],
        }
    },

    methods: mapActions(['addCards']),

    computed: {
        ...mapGetters(['getAllCards']),
    },
    
    async mounted() {
        this.addCards()
        this.cards = await this.getAllCards
    },
    components: {
        Card, 
    }
}
</script>

<style scoped>
    .card-list {
        justify-content: center;
        height: 100%;
        display: grid;
        column-width: 300px;
        grid-template-columns: repeat(3, 300px);
        grid-template-rows: auto;
        gap: 30px;
    }

    @media screen and (max-width: 1040px) {
        .card-list {
            grid-template-columns: repeat(2, 300px);
        }

    }

    @media screen and (max-width: 670px ) {
        .card-list {
            grid-template-columns: repeat(1, 300px);
        }

    }
</style>