<template>
    <div>
        <ul>
            <AddTodoComponent
                v-if="place === 'editor'"
                v-bind:id="id"
            />

            <TodoItem 
                v-for="todo of todos"
                :todo="todo"
                :key="todo.id"
                v-bind:item="todo"
                v-bind:place="place"
                v-bind:id="id"
            />
        </ul>
    </div>
</template>

<script>
import TodoItem from '@/components/TodoItem'
import AddTodoComponent from '@/components/AddTodoComponent'
export default {
    props: ['todos', 'place', 'id'],
        components: {
            TodoItem, AddTodoComponent
    },
}
</script>

<style scoped>
    ul {
        list-style: none;
        margin: 0;
        padding: 0;
        margin-right: auto;
        margin-left: auto;
    }
</style>