import {allCards} from './allCards'
export default {
    actions: {

        addCards(ctx) {
            const cards = allCards
            ctx.commit('addCards', cards)
        },
    },

    mutations: { 
        
        addCards(state, cards) {
            if (sessionStorage.getItem('cards')) {
                state.cards = JSON.parse(sessionStorage.getItem('cards'))
            }
            else state.cards = cards
        },

        addCard(state, card) {
            state.cards.push(card)
            sessionStorage.setItem('cards', JSON.stringify(state.cards))
        },

        addTodo(state, obj) {
            state.cards[obj.id].todos.push(obj.todo)
            sessionStorage.setItem('cards', JSON.stringify(state.cards))
        },

        removeCard(state, id) {
            let array = state.cards;
            let lala = 0
            array.forEach(element => {
                if(element.id === id) {
                    lala = element
                }
            });
            const index = array.indexOf(lala)
            state.cards.splice(index, 1)   

            let i = 0
            state.cards.forEach(element => {
                element.id = i
                i++
            })
            sessionStorage.setItem('cards', JSON.stringify(state.cards))
        },

        removeTodo(state, obj) {
            const todos = state.cards[obj.id].todos
            state.cards[obj.id].todos = todos.filter(item => item != obj.item)
            sessionStorage.setItem('cards', JSON.stringify(state.cards))
        }
    },

    state: {
        cards: [
        ]
    },

    getters: {

        getAllCards(state) {
            return state.cards
        },

        getCardToEdit(state) {
            return index => state.cards[index]
        }

    }, 
}