<template>
    <div class="addCard">
        <form @submit.prevent="submit">
            <input type="text" placeholder="название заметки" v-model="title">
            <input type="text" placeholder="введите пункты через ' ; '" v-model="todos">
            <button type="submit"> create card </button>
        </form>
    </div>
    
</template>

<script>
import { mapMutations } from 'vuex'
export default {
    data() {
        return {
            title: '',
            todos: ''
        }
    },

    methods: {
        ...mapMutations(['addCard']), 

        submit() {
            if (this.title.trim()) {
                let card = {}
                card.id = this.$store.state.cards.cards.length
                card.title = this.title
                
                card.todos = []
                let newArr = this.todos.split(';')
                newArr.forEach(el => {
                    card.todos.push(el.trim())
                })
                card.todos = card.todos.filter(el => el != '')
                this.addCard(card)

                this.$emit('close', card.id)
            }
        }
    }
}
</script>

<style scoped>
    .addCard {
        align-items: center ;
        padding: 30px;
    }

    form {
        margin-left: auto;
        margin-right: auto;
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;
        width: 200px;
    }

    button {
        cursor: pointer;
    }
</style>