<template>
    <div>
        <li class="item "
        v-bind:class=" {done: done} "> 

            <input class="checkbox" type="checkbox" 
            v-if="place === 'editor'"
            v-model="done"
            >

            {{ item }} 

            <button class="del" 
            v-if="place === 'editor'"
            @click="del(id, item)"
            > x </button>
            
        </li>
    </div>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
    props: ['item', 'place', 'id'],

    data() {
        return {
            done: false,
            obj: {}
        }
    }, 

    methods: {
        ...mapMutations(['removeTodo']),

        del(id, item) {

            this.obj.id = parseInt(id)
            this.obj.item = item
            this.removeTodo(this.obj)
        }
    }
   
}
</script>

<style scoped>
    .item {
        border: 1px solid #ccc;
        display: flex;
        justify-content: space-between;
        padding: .5rem 2 rem;
        margin-bottom: 1rem; 
    }

    .checkbox {
        height: 15px;
        width: 15px;
    }

    .del {
        height: 20px;
        width: 20px;
        background-color: red;
        color: #fff;
        border-radius: 50%;
        border: none;
    }

    .done {
        text-decoration: line-through;
    }

    input {
        margin-right: 1rem;
    }
</style>