<template>
    <form @submit.prevent="onSubmit">
        <input type="text" placeholder="название пункта" v-model="todo">
        <button type="submit">create</button>
    </form>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
    data() {
        return {
            todo: ''
        }
    },

    props: ['id'],
    methods: {
        ...mapMutations(['addTodo']),
        onSubmit() {
            if (this.todo.trim())   {

                let array = this.$store.state.cards.cards;
                let lala = 0;
                array.forEach(element => {
                    if(element.id === this.id[0]) {
                        lala = element
                    }
                });
                const index = array.indexOf(lala);
                
                const obj = {                    
                    id: index,
                    todo: this.todo
                }
                this.addTodo(obj)
                this.todo = ''
            }
        }
    }
}
</script>

<style scoped>
    input {
        width: 30%;
        margin-right: auto;
        margin-left: auto;
        margin-bottom: 20px;
    }

    button {
        cursor: pointer;
    }
</style>