<template>
    <div>
        <h2> все заметки </h2>

        <button class="showPopupButton" @click="show = true"> добавить карточку </button>

        <div class="container">

            <CardList/>

            <AddCardPopup
                v-if="show"
                @close="closePopup()"
            />

        </div>
    </div>
</template>
<script>
import CardList from '@/components/CardList'
import AddCardPopup from '@/components/AddCardPopup'
export default {
    data() {
       return {
           show: false
       } 
    },

    methods: {
        closePopup() {
            this.show = false
        }
    },

    components: {
        CardList, AddCardPopup
    }, 

}
</script>

<style scoped>
    .showPopupButton {
        margin: 20px auto;
        cursor: pointer;
    }
</style>